<template>
 
		  <div class="top-content">
   
  
    <el-input v-model="username" placeholder="用户名"></el-input>
 
 
    <el-input v-model="password" placeholder="密码"></el-input>
 
 
    <el-button type="primary" @click="login">登陆</el-button>
   
        </div>
  
 

 
</template>
  
 
 
 

<script>
import { setCookie,getCookie } from '../../assets/js/cookie.js'
	export default{
		data(){
			return{
				
			 username: '',
				password: '',
				newUsername: '',
				newPassword: '',
				tishi: '',
				showTishi: false,
				showLogin: true,
				showRegister: false,
				blog:[],
		 	}
		},
		mounted(){
			if(getCookie('username')){
				this.$router.push('/home')

			}
				let date=new Date()
	           console.log(date.getSeconds())
		},
		methods: {
			login(){
				if(this.username == "" || this.password == ""){
					alert("请输入用户名或密码")
				}else{
					let data = {'username':this.username,'pwd':this.password}
					
					this.$http.post('http://120.76.196.155:30',data).then((res)=>{
			 	         console.log(res)
						if(res.data == -1){
							this.tishi = "该用户不存在"
							this.showTishi = true
						}else if(res.data == 0){
							this.tishi = "密码输入错误"
							this.showTishi = true
						}else if(res.data == 'admin'){
							this.$router.push('/main')
						}else {
							this.tishi = "登录成功"
							this.showTishi = true
							setCookie('username',this.username,1800)
							setTimeout(function(){
								this.$router.push({path:'home',query:{id:1}})
								//this.$router.go(0)
							}.bind(this),1000)
						}
					})
				}
			},
			ToRegister(){
				this.showRegister = true
				this.showLogin = false
			},
			ToLogin(){
				this.showRegister = false
				this.showLogin = true
			},
	 
		}
	}
</script>


