<template>
  <div class="top-content">
  	<div class="top-content">
   
  
    <el-input v-model="newUsername" placeholder="用户名"></el-input>
 
 
    <el-input v-model="newPassword" placeholder="密码"></el-input>
 
 
    <el-button type="primary" @click="register">注册</el-button>
   
        </div>
       </div> 	
            
</template>
  
 

<script>
import { setCookie,getCookie } from '../../assets/js/cookie.js'
 
	export default{
		data(){
			return{
				username: '',
				password: '',
				newUsername: '',
				newPassword: '',
				tishi: '',
				showTishi: false,
				showLogin: false,
				showRegister: true
			}
		},
		mounted(){
			if(getCookie('username')){
				this.$router.push('/home')
					this.$router.go(0)
			}
		},
		methods: {
			ToRegister(){
				this.showRegister = true
				this.showLogin = false
			},
			ToLogin(){
				this.showRegister = false
				this.showLogin = true
			},
			register(){
				if(this.newUsername == "" || this.newPassword == ""){
					alert("请输入用户名或密码")
				}else{
					let data = {'username':this.newUsername,'password':this.newPassword}
					this.$http.post('http://localhost/vueapi/index.php/Home/user/register',data).then((res)=>{
						console.log(res)
						if(res.data == "ok"){
							this.tishi = "注册成功"
							this.showTishi = true
							this.username = ''
							this.password = ''
							setTimeout(function(){
								this.showRegister = false
								this.showLogin = true
								this.showTishi = false
							}.bind(this),1000)
						}
					})
				}
			}
		}
	}
</script>


