<template>
    <div class="record">
   
        <div class="lottie" v-loading="loading" element-loading-text="配乐中">
             <lottie :options="defaultOptions" :height="200" :width="200" v-on:animCreated="handleAnimation" 
              @click.native="judge"/>
         </div>
        <div class="record-play"   v-show="isFinished">
            
            <audio controls autoplay></audio>
            <a href="" download="文件.wav" id="btn">下载</a>
        </div>
       <div v-show="false">
       <button v-on:click="startRecord">录音</button>
         <button v-on:click="stopRecord">停止和上传</button>
          <button v-on:click="getsrc">获取</button>
   </div>

        <div class="big2" v-show="false" >
            <h2> &nbsp;&nbsp;&nbsp;  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;   </h2>
          <audio-player :src="getsrc()"/>
        </div>
            
    </div>

</template>
 <style type="text/css">
.big2{
    position: absolute;
        width: 350px;
        height: 500px;
        margin: auto;
        top: 2;
        left: 0;
        bottom: 0;
        right: 0;
}
 
 </style>


<script>
    import Record from "../../commons/record-sdk";
    import { setCookie,getCookie } from '../../assets/js/cookie.js';
    import * as animationData from "../../assets/mircro.json";

    export default {
        name: "Record",
    
        data() {
            return {
                isFinished: false,
                musicisFinished:false,
                now:false,
                audio: "",
                recorder: new Record(),
                loading:false,
               defaultOptions: { animationData: animationData,                                                                               },
                    

            };
        },
        mounted(){
  

              
        },
        methods: {
              handleAnimation: function(anim) {
                this.anim = anim;
                  this.anim.stop(); 
                console.log(anim); //这里可以看到 lottie 对象的全部属性
            },
      stop: function () {
        this.anim.stop();      
      },

      play1: function () {
        this.anim.play();
      },

      pause: function () {
        this.anim.pause();
      },
     judge:function(){
         let self = this;
       if( self.now==false) {
         this.anim.play();
         self.now=true;
         this.$options.methods.startRecord.bind(this)();
       }
       else{
        this.anim.stop();
        self.now=false;
        self.loading=true;
        this.$options.methods.stopRecord.bind(this)();
       this.$options.methods.play.bind(this)();
       }

     },






            startRecord: function() {
                console.log("start to record now.");
                let self = this;
                self.isFinished = false;
                self.recorder.startRecord({
                    success: res => {

                        console.log("start record successfully.");
                    },
                    error: res => {
                        console.log("start record failed.");
                    }
                });
            },
            stopRecord: function() {
                console.log("stop record now.");
                let self = this;
                self.isFinished = false;
                self.recorder.stopRecord({
                    success: res => {
                        //此处可以获取音频源文件(res)，用于上传等操作
                    // console.log(res);
                         document.getElementById("btn").href = window.URL.createObjectURL(res);
                         let fd=new FormData();
                         let file = new File([res], "20190603.wav");
                         fd.append("audioData", file);
                       //  fd.append("name", "123");
               
                         this.$http.post('http://120.76.196.155:3000/uploa',fd).then((res)=>{
                        console.log(res)
                       
                         if(res.data.id== 101 ){
                            self.musicisFinished= true;
                        }
                         
                    })
                        console.log("stop record successfully.");
                    },
                    error: res => {
                        console.log("stop record failed.");
                    }
                });
            },
            play: function() {
                console.log("play record now.");
                let self = this;
                self.isFinished = true;
                self.audio = document.querySelector("audio");
                self.recorder.play(self.audio);

            },
            getRecord: function() {

                  this.$http.get('http://jsonplaceholder.typicode.com/posts').then((res)=>{
                
                   console.log(res.data.length);

       
       
       
      })

            },
            getsrc: function() {

                let a= "https://cdn.moefe.org/music/mp3/thing.mp3";
                return a;

            },
          


        }
    };
</script>

 



