<template>
  
  <div id="app">

      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" router=true>
  <el-menu-item index="/">首页</el-menu-item>
 <el-menu-item index="Main">配乐</el-menu-item>
    <el-menu-item index="login">登陆</el-menu-item>  
  <el-menu-item index="register">注册</el-menu-item>  
 <el-menu-item index="home"><a v-show="!unFinished">个人页面</a></el-menu-item>  
 <el-menu-item ><a @click="quit" v-show="!unFinished">注销</a></el-menu-item>  
</el-menu>
    <router-view></router-view> 
  </div>


  
</template>

<script>
import { setCookie,getCookie,delCookie } from '../src/assets/js/cookie.js'
export default {

  name: 'app',
   data(){
            return{
              unFinished: true,
              activeIndex: '1',
     
        
            }
        },
      mounted(){
      if(getCookie('username')){
          let self = this
          self.unFinished = false
          
            
      }
    },
  methods:{
     handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
   
    setCookie: function(c_name,value,expire) {
        var date=new Date()
        date.setSeconds(date.getSeconds()+expire)
        document.cookie=c_name+ "="+escape(value)+"; expires="+date.toGMTString()
        console.log(document.cookie)
      },
      getCookie: function(c_name){
        if (document.cookie.length>0){
          let c_start=document.cookie.indexOf(c_name + "=")
          if (c_start!=-1){ 
              c_start=c_start + c_name.length+1 
              let c_end=document.cookie.indexOf(";",c_start)
              if (c_end==-1) c_end=document.cookie.length
              return unescape(document.cookie.substring(c_start,c_end))
          } 
        }
        return ""
      },
       //跳转主页页面
           index: function(){        
             this.$router.push('/index')
         
            },
            //跳转录音页面
            home: function(){        
             this.$router.push('/Main')
      
            },
            //跳转登陆页面
            login: function(){        
             this.$router.push('/')
          
            },
            //跳转注册页面
            register: function(){        
             this.$router.push('/register')   
       
            },
            //个人界面
            personal: function(){        
            this.$router.push('/home')  
          
            },
            //注销操作
            quit: function(){
            delCookie('username')
            let self = this
            self.unFinished = true
            this.$router.push('/login')      
            }
            //折叠导航栏
      /*      scroll: function(){
              if($(window).width() < 768){
                //点击导航链接之后，把导航选项折叠起来
                $("#navbar a").click(
                $("#navbar").collapse('hide')
               )                    
              }
            }      */
  }  
}
</script>





